import axios from 'axios';
//发布商品
const release = (data) => axios.post('/seller/goods', data);

//删除商品
const remove = (id, params) => axios.delete(`/seller/goods/${id}`, { params });

//编辑商品
const update = (id, data) => axios.put(`/seller/goods/${id}`, data);

//商品列表
const list = (params) => axios.get('/seller/goods/list', { params });

//商品详情
const detail = (params) => axios.get('/seller/goods', { params });

export default {
    release,
    remove,
    update,
    list,
    detail,
}