import axios from 'axios';

// 添加分类
const insert = (data) => axios.post('/category', data);

// 删除分类
const remove = (id, params) => axios.delete(`/category/${id}`, { params });

// 更新分类
const update = (id, data) => axios.put(`/category/${id}`, data);

// 获取子级分类
const load = (params) => axios.get('/category/sub', { params });

export default {
    insert,
    remove,
    update,
    load,
}
