<template>
  <el-menu :collapse="isCollapse" :default-openeds="['01']" background-color="#334e66" router text-color="#fff"
           active-text-color="#ffd04b">
    <template v-for="(item,index) in sideMenu">

      <el-menu-item v-if="item.path" :index="item.path">
        <i :class="`el-icon-${item.icon_name}`"></i>
        <span slot="title">{{ item.name }}</span>
      </el-menu-item>

      <el-submenu v-else :index="index + '1'">
        <template slot="title">
          <i :class="`el-icon-${item.icon_name}`"></i>
          <span slot="title">{{ item.name }}</span>
        </template>
        <el-menu-item v-for="element in item.children" :index="element.path">
          <i :class="`el-icon-${element.icon_name}`"></i>
          <span slot="title">{{ element.name }}</span>
        </el-menu-item>
      </el-submenu>

    </template>

  </el-menu>
</template>

<script>
import { Role } from "@/api/";
import { mapState } from 'vuex';

export default {
  name: "LeftMenu",
  data() {
    return {
      sideMenu: [],
    }
  },
  computed: {
    ...mapState('Menu', ['isCollapse']),
  },
  created() {
    this.loadSideMenu();
  },
  methods: {
    async loadSideMenu() {
      // 提取角色id
      let id = sessionStorage.role;
      let { status, data } = await Role.menu({ id, type: 'tree' });
      if (status) {
        this.sideMenu = data;
      }
      // TODO 注册动态路由
    }
  }
}
</script>

<style scoped="scoped">
.el-menu:not(.el-menu--collapse) {
  width: 200px;
}
</style>
