import axios from 'axios';

// 角色列表
let list = (params) => axios.get('/role/list', { params })

// 添加角色
let insert = (data) => axios.post('/role', data);

// 删除角色
let remove = (id, params) => axios.delete(`/role/${id}`, { params });

// 编辑角色
let edit = (id, data) => axios.put(`/role/${id}`, data);

// 侧边栏菜单
let menu = (params) => axios.get('/role/menu/', { params });

//根据角色id获取菜单配置
const loadConfig = (params) => axios.get('/role/config', { params });

// 设置角色菜单
let setMenu = (data) => axios.post('/role/menu', data);

//为指定角色添加菜单
const createMenu = (data) => axios.post('/role/menu', data);

//为指定角色删除菜单
const removeMenu = (id, params) => axios.delete(`/role/menu/${id}`, { params });

export default {
    list,
    insert,
    remove,
    edit,
    menu,
    loadConfig,
    setMenu,
    createMenu,
    removeMenu,
}